import {AnimationTriggerMetadata, query, transition, trigger, useAnimation} from '@angular/animations';
import {ANIMATION_NAME} from '../common/animation';

/**
 * Modal/dialog animation
 */
export const ModalAnimation: AnimationTriggerMetadata[] = [
  trigger('modalAnimation', [
    transition('void => *', [
      query('.modal-dialog', [
        useAnimation(ANIMATION_NAME.popIn)
      ])
    ]),
    transition('* => void', [
      query('.modal-dialog', [
        useAnimation(ANIMATION_NAME.popOut)
      ])
    ])
  ]),
  trigger('backdropAnimation', [
    transition(
      'void => *', [
        query('.modal-backdrop', [
          useAnimation(ANIMATION_NAME.opacity, {
            params: {
              from: '0',
              to: '*'
            }
          })
        ], {optional: true})
      ]
    ),
    transition(
      '* => void', [
        query('.modal-backdrop', [
          useAnimation(ANIMATION_NAME.opacity, {
            params: {
              from: '*',
              to: '0'
            }
          })
        ], {optional: true})
      ]
    )
  ])
];
